.skhead {
    border-bottom: 3px solid #323541;
    padding-top: 30px;
    padding-bottom: 30px;
    font-family: 'Outfit', sans-serif;
    text-align: center;
}

.htxt {
    font-family: 'Lato', sans-serif;
    font-size: 15px;
    text-align: center;
    line-height:2;
}

.htxt-box {
    padding:50px;
}

.card-cont {
    margin-top: 50px;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.card {
    background-color: #1d2128;
    border: 3px solid #323541;
    border-radius: 15px;
    margin: 15px;
}

.card-size {
    width:30rem;
    max-width: 90vw;
}