.desc {
    display: flex;
    justify-content: center;
    align-items: center;
    margin:50px;
}

.desc-cont {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90vw;
    max-width: 1200px;
    gap: 30px;
}

.abt-pic {
    max-width: 300px;
    width: 90vw;
    align-self: center;
}

.abt-txt {
    max-width: 500px;
}

.extra {
    display: flex;
    justify-content: center;
    align-items: center;
    margin:50px;
    margin-bottom: 100px;
}

.extra-cont {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90vw;
    max-width: 800px;
    gap: 50px;
}

.extra-part {
    margin: auto;
    margin-top: 0px;
    display: flex;
    flex-direction: column;
    align-items: start;
}