.phead {
    font-family: 'Outfit', sans-serif;
    text-align: center;
    margin-bottom:10px;
}

.pcap {
    font-size:1em;
    background-color:#575558;
    color:#a9a6ab;
    border-bottom-left-radius:15px;
    border-bottom-right-radius:15px;
}

.proj {
    margin-top:100px;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items:center;
}

.proj-body {
    width:1200px;
}

.proj-sect {
    margin:50px;
    margin-bottom:100px;
    display:flex;
    max-width:1200px;
    align-items:center;
    justify-content:space-between;
}

.proj-img-cont {
    width:450px;
    max-width:90vw;
    margin:auto;
}

.proj-txt {
    width:90vw;
    max-width:450px;
    margin:30px;
    display:flex;
    align-items:center;
}

.proj-img {
    width:450px;
    max-width:90vw;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
}

.dist-cont {
    display: flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}

.dist {
    margin-top:30px;
    width:80vw;
    max-width:1200px;
    display: flex;
    flex-direction: row;
    justify-content:space-evenly;
    flex-wrap:wrap;
    gap:20px;
}

.dist-sub {
    display:flex;
    flex-direction:row;
}

.cour-cont {
    margin-top:100px;
    margin-bottom:75px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cour {
    margin-top:30px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap:15px;
}

.cour-sub-comp {
    border-radius: 15px;
    background-color: #1cff3e80;
    padding: 10px;
}

.cour-sub-prog {
    border-radius: 15px;
    background-color: #ffa01c80;
    padding: 10px;
}

.cour-part {
    display: flex;
    flex-direction: column;
    gap:10px;
    margin: auto;
}

.t-txt {
    font-size:12px;
}

@media screen and (max-width:1120px) {
    .proj-sect {
        display:block;
    }

    .proj-txt {
        margin:auto;
        margin-bottom:30px;
    }
}