:root {
    /*
    --bg: #454b66;
    --bg-accent: #5b6386;*/
    --bg: #222427;
    --bg-accent: #3a3d49;
    --text-color: #F7F7F2;
    --nav-size: 60px;
    --border: 1px solid #34394c;
    --border-radius: 8px;
    --speed: 500ms;
}

.homeButton {
    margin-left: 10px !important;
}

.smallSee-icon {
    display:none;
    width: 0px !important;
    padding: 0px !important;
    margin: 0px !important;
    pointer-events: none;
}

.smallSee-drop {
    display:none;
    width: 0px;
    padding: 0px !important;
    border: 0px !important;
    pointer-events: none;
}

.bigSee {
    display: block;
    pointer-events: auto;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    color: var(--text-color) !important;
    text-decoration: none;
}

.nav-h3 {
    margin-bottom: 0;
}

.navb {
    display:flex;
    justify-content:center;
    align-items:center;
    height: var(--nav-size);
    background-color: var(--bg);
    padding: 0 1rem;
    border-bottom: var(--border);
}

.navb-nav {
    max-width:1500px;
    height: 100%;
    display: flex;
    justify-content: flex-end;
}

.navb-item {
    /*width: calc(var(--nav-size)*0.8);*/
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 5vw;
}

.icon-button {
    /*--button-size: calc(var(--nav-size)*0.5);*/
    width: var(--button-size);
    height: var(--button-size);
    color: var(--text-color);
    /*background-color: #484a4d;
    border-radius: 50%;*/
    padding: 5px;
    margin: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: filter 300ms;
}

.icon-button:hover {
    filter: brightness(1.2);
}

.icon-button svg {
    fill: var(--text-color);
    width: 20px;
    height: 20px;
}

.dropdown {
    display: block;
    position: absolute;
    top: 60px;
    background-color: var(--bg);
    border: var(--border);
    /*border-radius: 0 0 var(--border-radius) var(--border-radius);*/
    padding: 1rem;
    overflow:hidden;
    transition: height var(--speed) ease;
    z-index:1000;
}

.rounded {
    border-radius: var(--border-radius);
}

.menu {
    width:100%;
}

.menu-item {
    height:50px;
    display:flex;
    align-items: center;
    color: var(--text-color);
    border-radius: var(--border-radius);
    transition: background var(--speed);
    padding:1rem;
}

.menu-item .icon-button {
    margin-right: 0.5rem;
}

.menu-item .icon-button:hover {
    filter:none;
}

.menu-item:hover {
    background-color: var(--bg-accent);
    color:var(--text-color);
}

.icon-left {
    margin-left: 0.5rem;
    margin-right: 1rem;
    color:var(--text-color);
}

.icon-right {
    margin-left: auto;
    margin-right:0.5rem;
    color:var(--text-color);
}

.menu-primary-enter {
    position: absolute;
    transform:translateX(-110%);
}

.menu-primary-enter-active {
    transform:translateX(0%);
    transition: all var(--speed) ease;
}

.menu-primary-exit {
    position: absolute;
}

.menu-primary-exit-active {
    transform:translateX(-110%);
    transition:all var(--speed) ease;
}

.menu-secondary-enter {
    transform:translateX(110%);
}

.menu-secondary-enter-active {
    transform:translateX(0%);
    transition: all var(--speed) ease;
}

.menu-secondary-exit {
}

.menu-secondary-exit-active {
    transform:translateX(110%);
    transition:all var(--speed) ease;
}

.normalDrop {
    width: 125px;
}

@media screen and (max-width:720px) {
    .smallSee-icon {
        display: flex;
        width: var(--button-size) !important;
        padding: 5px !important;
        margin: 2px !important;
        pointer-events: auto;
    }
    .smallSee-drop {
        display:block;
        pointer-events:auto;
        left:50%;
        width: 100% !important;
        padding:1rem !important;
        border: var(--border) !important;
        transform: translateX(-50%);
    }
    .bigSee {
        display: none !important;
        width: 0px !important;
        pointer-events: none !important;
    }
    .homeButton {
        margin-left: 10px !important;
        margin-right: auto;
    }
    .press {
        width: 100%;
    }
}