/* original: #5ea906 */

.backButton {
    color: black;
    width: 25px;
    height: 25px;
}

.curveBack {
    position: relative;
    background-color: #121113;
    width:100%;
    height:450px;
    padding: 150px;
}

.unitBack {
    position: relative;
    background-color: #121113;
    display:flex;
    width:100%;
    /*min-height:20%;*/
    height: 350px;
}

.textBack {
    position: relative;
    padding:30px;
    margin: auto;
    overflow: hidden;
}

.wave {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

.wave svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 64px;
}

.wave .shape-fill {
    fill: rgb(50,50,50);
}

.curve {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

.curve svg {
    position: relative;
    display: block;
    width: 100%;
    height: 5%;
}

.curve .shape-fill {
    fill: rgb(50,50,50);
}