body {
    margin: 0;
    overflow-x: hidden;
}

.vert-center {
    margin: 0;
    position: absolute;
    top: 50%;
    -ms-transform:translateY(-50%);
    transform:translateY(-50%);
}

.hori-center {
    margin:0;
    position:absolute;
    left:50%;
    -ms-transform:translateX(-50%);
    transform:translateX(-50%);
}

.center {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
}