.footer {
    background-color: #222427;
    margin-top: 20px;
    align-items: center;
    justify-content: center;
    padding-bottom: 30px;
}

.mailcont {
    background-color: #15191f;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px;
}

.cont {
    display: flex;
    width: 100%;
    max-width: 750px;
    justify-content: space-between;
    margin: auto;
    margin-top: 20px;
    padding: 20px;
    padding-left:30px;
    padding-right:30px;
}

.logo {
    align-self: left;
    margin-top: auto;
    margin-bottom: auto;
}

.logo-img {
    width:25vw;
    max-width:150px;
}

.links {
    align-self: right;
    display: inline-flex;
    margin-top: auto;
    margin-bottom: auto;
}

.link-sect {
    margin-left: 50px;
}

.lnk {
    display: block;
    margin-bottom: 5px;
}

.fhead {
    margin-bottom: 10px;
}